import React, {useState, useEffect} from "react"
// import {Router, Link, Location } from "gatsby"

import SEO from "../components/SEO"

import LayoutFlex from "../components/layout-flex"
import PropertyBox from "../components/property-box"
import Canvas from "../components/canvas"

import * as Properties from "../components/property-constants"


let expandItem = [];

function IndexPage(props) {
    let sampleKey = props.location.hash.split("#");
    sampleKey = sampleKey[sampleKey.length-1];

    let dataArray = JSON.parse(JSON.stringify(Properties.propertiesDataArray));
    dataArray['display'].value = 'flex'; //initialize for default canvas
    dataArray['flex-grow'].value = 1; //initialize for default canvas
    const dataDefault = JSON.parse(JSON.stringify(propertiesDefault));
    const [propertyData, setPropertyData] = useState([dataArray]);
    const [id, setId] = useState(0);
    const [sample, setSample] = useState(sampleKey);
    const [info, setInfo] = useState(null);

    useEffect(() => {
        if (sampleKey && Properties[sampleKey]) {
            let data = [JSON.parse(JSON.stringify(Properties[sampleKey]))];
            setPropertyData(data);
        }
      }, [sampleKey]);

    function sampleFormation(key) {
        setSample(key);
        let data = [JSON.parse(JSON.stringify(Properties[key]))];
        if (Properties[key].length > 0) {
            setPropertyData(JSON.parse(JSON.stringify(Properties[key])));
        }else{
            setPropertyData([...data]);
        }
        setId(0);
    }

    function resetCanvas() {
        let data = [dataArray];
        setSample(null)
        setPropertyData(data);
        setId(0);
        propertiesCloaseAll(0);
    }

    function resetProperties(itemID) {
        let data = [...propertyData];
        let dataArrayTemp = JSON.parse(JSON.stringify(Properties.propertiesDataArray));
        data[itemID] = dataArrayTemp;
        setPropertyData([...data]);
        expandItem.map(element =>{
            return element.style.maxHeight = null;
        })
        expandItem = [];
    }

    function propertiesExpand(key, element){
        let data = [...propertyData];
        data[id][key].expand = !data[id][key].expand;
        setPropertyData(data)
        if (data[id][key].expand) {
            element.style.maxHeight = element.scrollHeight + "px";
            if (!expandItem.find(item => item === element)) expandItem.push(element);
        } else {
            element.style.maxHeight = null;
        }
    }

    function propertiesCloaseAll(itemID){
        let data = [...propertyData];
        for (var key in data[itemID]) {
            data[itemID][key].expand = false;
        }
        setPropertyData([...data])

        expandItem.map(element =>{
            return element.style.maxHeight = null;
        })
        expandItem = [];
    }

    function itemSelected(key, item){
        if (key === 'align-content') {
            if (propertyData[id]['flex-wrap'].value === 'nowrap' || propertyData[id]['flex-wrap'].value === null) {
                setInfo('justContentFlex');
            }
        }
        const data = [...propertyData];
        data[id][key].value = item;
        setPropertyData([...data]);
    }

    function getId(idTemp){
        propertiesCloaseAll(idTemp);
        if (idTemp !== id) {
            propertiesCloaseAll(id);
        }
        if (idTemp == null) {
            let data = [...propertyData];
            let dataArrayTemp = JSON.parse(JSON.stringify(Properties.propertiesDataArray));
            data.push(dataArrayTemp)
            setPropertyData([...data])
        }
        idTemp = idTemp == null ? propertyData.length : idTemp;
        setId(idTemp)
        // console.log(propertyData);

        return idTemp;
    }
    let compileData = {...propertyData[id]}

    return(
        <LayoutFlex>
            <SEO/>
            <div className="center">
                <Canvas getId={getId} propertyData={propertyData} resetCanvas={resetCanvas} sampleKey={sample} setSample={setSample} info={info} setInfo={setInfo}/>
                <PropertyBox id={id} propertiesExpand={propertiesExpand} itemSelected={itemSelected} propertyDefault={dataDefault} propertyData={compileData} resetProperties={resetProperties} sampleFormation={sampleFormation}/>
                {/* <div className="info"></div> */}
            </div>
        </LayoutFlex>
        )
}
const propertiesDefault = {
    'display' : {'data' : ['flex', 'block']},
    'flex-direction' : {'data' : ['row', 'row-reverse', 'column', 'column-reverse']},
    'flex-wrap' : {'data' : ['nowrap', 'wrap', 'wrap-reverse']},
    'flex-grow' : {'data' : ['input_number']},
    'flex-shrink' : {'data' : ['input_number']},
    'justify-content' : {'data' : ['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly']},
    'align-content' : {'data' : ['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'stretch']},
    'align-items' : {'data' : ['flex-start', 'flex-end', 'center', 'baseline', 'stretch']},
    'align-self' : {'data' : ['auto', 'flex-start', 'flex-end', 'center', 'baseline', 'stretch']},
    'order' : {'data' : ['auto', 'input_number']}
}

export default IndexPage
