import React, {useState, useRef} from "react"

function PropertyBox (props){
    let [rawBox, setRawBox] = useState(false);
    let propertiesArrayTemp = [];
    let index = 0;
    let rawElement =useRef(null);
    let rawCopy = '';
    let rawCode = [];
    
    for (var key in props.propertyData) { 
        let item = props.propertyData[key];
        propertiesArrayTemp.push(
            <SingleElemenProperty 
                name={key} 
                data={props.propertyDefault[key].data} 
                default={item.default} 
                value={item.value} 
                expand={item.expand} 
                onClick={props.propertiesExpand} 
                onClickItem={props.itemSelected} 
                key={key+item.name} 
                arrId={index}
                rawBox={rawBox}/>)
        index++;
        if (props.propertyData[key].value !== null && props.propertyData[key].value !== props.propertyData[key].default) {
            rawCopy = rawCopy+'\n '+key+': '+props.propertyData[key].value+';';
            rawCode.push(<div key={key+'raw'}><span>{key}{': '}</span>{props.propertyData[key].value}<span style={{color: 'rgb(255, 255, 255)'}}>{';'}</span></div>);
        }
      }
    function copyScript(ref) {
        ref.select();
        ref.setSelectionRange(0, 99999);
        document.execCommand("copy");
        
    }
    function showRaw(show) {
        if(show == null){
            setRawBox(!rawBox);
        }else{
            setRawBox(show)
        }
    }
    return(
        <div className="tools-wrapper">
            <div className="property-header">
                <div className="reset-property" onClick={() =>{props.resetProperties(props.id)}}>
                    RESET
                </div>
                <div className="title">
                    FLEX
                    {/* <div className="by">SIMULATOR</div>     */}
                </div>
            </div>
            {/* <div className="desc">
                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quod velit doloremque ea. Fuga, impedit itaque vitae velit rerum neque. Beatae vel blanditiis cupiditate! Suscipit ad corporis quaerat optio! Libero, eos!
            </div> */}
            <div className="tools">
                <div className={rawBox ? "raw-script" : "raw-script hide"}>
                    <div className="script">
                        <div className="tools-copy" onClick={() => copyScript(rawElement.current)}>
                            Copy
                        </div>
                        <div className="script-close" onClick={() => showRaw(false)}>
                            ✕
                        </div>
                        <textarea ref={rawElement} value={rawCopy} readOnly/>
                        <div className="raw">
                            {rawCode}
                        </div>
                    </div>
                </div>
                {/* <div className="tools-copy">
                        <div className="copy-icon"></div>
                </div> */}
                <div className={rawBox ? "blur" : ""}>
                    {propertiesArrayTemp}
                </div>
            </div>
            <div className="raw-button-wrapper">
                <div className="raw-button" onClick={() => showRaw()}>Raw Script</div>
            </div>
            <div className="info">
                <h4>&lt; SAMPLE &gt;</h4>
                <div className="sample-wrapper">
                    <div className="one-big-panel sample" onClick={() => props.sampleFormation('centerFlex')}>
                        <div className="one-panel" style={{width:'60px', height:'60px'}}>Center</div>
                    </div>
                    <div className="one-big-panel sample" onClick={() => props.sampleFormation('wrapFlex')}>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel"></div>
                        <div className="sample-desc">Wrap</div>
                    </div>
                    <div className="one-big-panel sample" onClick={() => props.sampleFormation('growFlex')}>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel" style={{flexGrow : '2'}}></div>
                        <div className="mini-one-panel"></div>
                       <div className="sample-desc">Grow</div>
                    </div>
                    <div className="one-big-panel sample" onClick={() => props.sampleFormation('alignSelfFlex')}>
                        <div className="mini-one-panel"></div>
                        <div className="mini-one-panel" style={{alignSelf:'flex-end'}}></div>
                       <div className="sample-desc">Align-self</div>
                    </div>
                    {/* <div className="one-big-panel sample">
                       <div className="sample-desc">Grow</div>
                    </div> */}
                </div>
            </div>
            {/* <div className="separate"> test</div> */}
        </div>
    )
}
function SingleElemenProperty(props){
    let tranStyle = {
        transition: 'max-height 0.2s ease-out'
    }
    let optionItem = []
    let value = props.value ? props.value : props.default;
    const expandElement = useRef(null);

    if (props.data.length > 1) {
        props.data.map(item =>{ 
                return optionItem.push(item === value ? 
                    <li className="active" key={item}>{item}</li> :
                    <li onClick={() => props.onClickItem(props.name, item)} key={item}>{item}</li>)
        })
    }
    
    function checkValue(valueElement) {
        if (valueElement === null || valueElement === '') {
            props.onClickItem(props.name, props.default)
        }
    }

    const defaultValue = !isNaN(value) || value === 'input_number' ? 
        <div className={value === props.default ? 'input-container' : 'input-container modified'}>
            {value === 'input_number' ? 
                <input onChange={e => {props.onClickItem(props.name, e.target.value)}} onClick={event => event.stopPropagation()} type="number" value={props.value ? props.value : props.default} onBlur={e => checkValue(e.target.value)} autoFocus/>:
                <input onChange={e => {props.onClickItem(props.name, e.target.value)}} onClick={event => event.stopPropagation()} type="number" value={props.value ? props.value : props.default} onBlur={e => checkValue(e.target.value)}/>}
            </div> : 
        <span className={value === props.default ? 'properties' : 'properties modified'}>{value}</span>; 
    return(
        <div className={"tools-item-wrapper"}>
            <div className="tools-item" onClick={() => props.onClick(props.name, expandElement.current)}>
                <div>{props.name}:</div>
                        {defaultValue}
                <span>;</span>
            </div>
            <div className="properties-option" style={tranStyle} ref={expandElement}>
                <ul>
                    {optionItem}
                </ul>
            </div>
        </div>
    )
    
}


export default PropertyBox
