import React, {useState, useContext, useEffect} from "react"
import * as Formation from "./elemen-constants"

let selectedKey = null;
let selectedNested = null;
let isItARock = false;
// let infoShow = null;
let dataStyle =  React.createContext(null);

function Canvas(props){
    let [canvasElement, setCanvasElement] = useState([]);
    let [infoShow, setInfoShow] = useState([]);
    // let [titleShow, setTitleShow] = useState('');
    let [sample, setSample] = useState(null);
    let info = [];
    dataStyle =  React.createContext(props.propertyData);

    useEffect(() => {  
        if (props.sampleKey && Formation[props.sampleKey] && sample !== props.sampleKey) {
            infoControl(props.sampleKey);
            selectElement(null)
            setSample(props.sampleKey);    
            const data = JSON.parse(JSON.stringify(Formation[props.sampleKey]));
            setCanvasElement([...data]);
            props.setSample(null);
        }else if(sample){
            setSample(null);
        }
        if (props.info) {
            infoControl(props.info);
            props.setInfo(null);
        }
    }, [props, sample]);

    function removeInfo(key) {
        if (infoShow && infoShow[0].key === key) {
            infoShow.shift()
            setInfoShow([...infoShow]);
        }
    }
    function resetCanvas() {
        setCanvasElement([]);
        selectedKey = null;
        selectedNested = null;
        props.resetCanvas();
    }
    function deleteElement(key) {     
        let data = [...canvasElement];
        let nestedTemp = null;
        if (key) {
            nestedTemp = data[key[0]];
            if (key.length > 1) {
                key.forEach((element, index) => {
                    if (index === key.length-1) {
                        if (selectedNested && selectedNested.ID === nestedTemp.children[element].ID) {
                            selectElement(null)
                        }
                        nestedTemp = nestedTemp.children.splice(element, 1);
                    }
                    else if (index === 0) {
                        nestedTemp = data[element];
                    } else {
                        nestedTemp = nestedTemp.children[element];
                    }
                });
            }else{
                if (selectedNested && selectedNested.ID === nestedTemp.ID) {
                    selectElement(null)
                }
                nestedTemp = data.splice(key[0], 1);
            }
        }else{
            nestedTemp = data;
        }

        if (selectedKey && key.length - selectedKey.length === 1) {
            key.pop();
            if (JSON.stringify(selectedKey) !== JSON.stringify(key)) {
                selectElement(null);
            }
        }else{
            selectElement(null);
        }
        setCanvasElement([...data]);
    }
    function addElement(eType){
        if (isItARock) {
            selectElement(null);
        }
        let data = [...canvasElement];
        let selectedChild = data;
        if (selectedKey){
            selectedKey.forEach((element, index) => {
                if (index === 0) {
                    selectedChild = data[element].children
                } else {
                    selectedChild = selectedChild[element].children
                }
            });
        }
        if (eType === "flexible-panel") infoControl('flexContainer');
        selectedChild.push({
            'class' : eType,
            //'color' : 'sample',
            'color' : canvasElement.length > 0 ? getRandomizeColor(3) : '',
            'children' : [],
        });
        // console.log(JSON.stringify(data));
        
        setCanvasElement([...data]);
    }
    function infoControl(key) {

        let data = {
            'info' : infoArray[key],
            'key' : key
        }
        infoShow = [];
        infoShow.push(data);
        setInfoShow([...infoShow]);
    }
    function selectElement(key, aRock = false) {
        isItARock = aRock;
        let data = [...canvasElement];
        if (selectedKey || selectedKey === 0) selectedNested.selected = false;
        if (key) {
            selectedKey = key;
            selectedNested = data[selectedKey[0]]
            if (key.length > 1) {
                key.forEach((element, index) => {
                    if (index === 0) {
                        selectedNested = data[element]
                    } else {
                        selectedNested = selectedNested.children[element]
                    }
                });
            }       
            selectedNested.ID = props.getId(selectedNested.ID);                
            selectedNested.selected = true;
        }else{
            props.getId(0);      
            selectedKey = null;
            selectedNested = data;
        }
        setCanvasElement([...data]);
        // console.log(JSON.stringify(data));
        
    }
    function getRandomizeColor(){
        let colorRange = [255, 85];
        var rgb = [Math.random() * colorRange[0] + colorRange[1], Math.random() * colorRange[0] + colorRange[1], Math.random() * colorRange[0] + colorRange[1]];
        return "rgb(" + rgb.join(",") + ")";
    }
    const styleTemp = useContext(dataStyle);

    let rawElement = [];
    if (canvasElement) {    
        canvasElement.map((data, index)=>{
            return rawElement.push(data.class === 'flexible-panel' ? 
                <FlexiblePanel dataStyle={styleTemp[data.ID]} nested={[index]} key={index} data={data} onclick={selectElement} deleteElement={deleteElement} infoControl={infoControl}/> :
                <ElementBox dataStyle={styleTemp[data.ID]} nested={[index]} key={index} data={data} onclick={selectElement} deleteElement={deleteElement} infoControl={infoControl}/>)}
        )
    }

    const fpStyle = {};
    for (var key in props.propertyData[0]) {
        fpStyle[props.propertyData[0][key].property] = props.propertyData[0][key].value ? props.propertyData[0][key].value : props.propertyData[0][key].default;
    }

    if (infoShow) {
        infoShow.map((data, index) => {
            return info.push(
                <InfoBox info={data} key={data.key+'info'} removeInfo={removeInfo}></InfoBox>
            )
        })
    }

    return(
        <div className="canvas-wrapper">
            <div className="instagram-wrapper">
                {/* <div className="instagram-logo"></div> */}
                {/* <div className="username">postlaz</div> */}
                by <span><a href="">@postlaz</a></span>
            </div>
            <div className="reset-canvas" onClick={() => resetCanvas()}>RESET</div>
            <div className="addition"><h4>Add : </h4>
                <button className="add-panel" onClick={() => addElement('flexible-panel')}>
                    <div className="mini-flexible-panel"></div>
                </button>
                <button className="add-panel" onClick={() => addElement('one-panel')}>
                    <div className="mini-one-panel"></div>
                </button>
                <button className="add-panel" onClick={() => addElement('one-big-panel')}>
                    <div className="mini-one-big-panel"></div>
                </button>
                <button className="add-panel" onClick={() => addElement('two-panel-hr')}>
                        <div className="mini-two-panel-hr"></div>
                </button>
                <button className="add-panel" onClick={() => addElement('two-panel-vr')}>
                    <div className="mini-two-panel-vr"></div>
                </button>
            </div>
            <div className="scroll-canvas-wrapper">
                <div className="scroll-canvas">
                    <div style={fpStyle} className="canvas" id="canvas" onClick={() => selectElement(null)}>
                        {rawElement}
                    </div>
                    {info}
                </div>
            </div>
        </div>  
    )
}
function ElementBox(props) {
    let who = '';
    if (props.data.class === "one-panel") {
        who = "a rock";
    }else if(props.data.class === "one-big-panel"){
        who = "a big rock";
    }else if(props.data.class === "two-panel-hr"){
        who = "a long rock";
    }else if(props.data.class === "two-panel-vr"){
        who = "a tall rock";
    }

    let fpStyle = {
        backgroundColor : props.data.color,
        // box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.4);
        boxShadow : props.data.selected ? '0px 0px 10px 2px rgba(255, 0 , 0)' : ''
      };
    for (var key in props.dataStyle) {
        fpStyle[props.dataStyle[key].property] = props.dataStyle[key].value ? props.dataStyle[key].value : props.dataStyle[key].default;   
    }
    return(
        <div className={props.data.class} style={fpStyle}>
            <div className="close-wrapper" 
                onClick={(event)=>{
                    event.stopPropagation();
                    props.onclick(props.nested, true);
                    }}
                // onClick={(event)=>{
                //     event.stopPropagation();
                //     props.infoControl(0)}}
            >
                <div className="a-rock"> {who}</div>
                <div className="button" onClick={(event)=>{
                    event.stopPropagation();
                    props.deleteElement(props.nested);
                }}>✕</div>
            </div>
        </div>
    )
}
function FlexiblePanel(props) {
    let childElement = [];
    const styleTemp = useContext(dataStyle);
    if (props.data.children) {
        props.data.children.map((data, index)=>{
            return childElement.push(data.class === 'flexible-panel' ? 
                <FlexiblePanel dataStyle={styleTemp[data.ID]} nested={[...props.nested, index]} key={index} data={data} onclick={props.onclick} deleteElement={props.deleteElement} infoControl={props.infoControl}/> :
                <ElementBox dataStyle={styleTemp[data.ID]} nested={[...props.nested, index]} key={index} data={data} onclick={props.onclick} deleteElement={props.deleteElement} infoControl={props.infoControl}/>
            )}
        )
    }
    let fpStyle = {
        // border : props.data.selected ? '3px solid gold' : '',
        backgroundColor : props.data.selected ? 'yellow' : '',
        boxShadow : props.data.selected ? '0px 0px 10px 3px yellow' : ''
      };

    for (var key in props.dataStyle) {
        fpStyle[props.dataStyle[key].property] = props.dataStyle[key].value ? props.dataStyle[key].value : props.dataStyle[key].default;   
    }
    return(
        <div className={props.data.class+' cover-child'} style={fpStyle}
             onClick={(event)=>{
                event.stopPropagation();
                props.onclick(props.nested);
             }}>
                <div className="flexible-close-wrapper">
                    <div className="close-wrapper">
                        <div className="button" onClick={(event)=>{
                            event.stopPropagation();
                            props.deleteElement(props.nested);
                        }}>✕</div>
                    </div>
                </div>
                 {childElement}
                 {/* <div style={{position : 'absolute'}}>{JSON.stringify(fpStyle)}</div> */}
        </div>
    )
}

function InfoBox(props) {
    useEffect(() => {
        const timer = setTimeout(() => {
          props.removeInfo(props.info.key);
        }, 5000);
        return () => clearTimeout(timer);
      }, []);
    return(
        <div className="formationTitle">
            <p>{props.info.info}</p>
        </div>
    )   
}

const infoArray = {
    'rock' : 'It\'s a rock. Just for dummies, you can\'t change its attributes.',
    'centerFlex' : <span>Center horizontally and vertically. <ol><li><span className="info-attributes">justify-content</span> is set to <span className="info-style modified">center</span></li><li><span className="info-attributes">align-items</span> is set to <span className="info-style modified">center</span></li></ol></span>,
    'justContentFlex' : <span>Align-content only works when <span className="info-attributes">flex-wrap</span> is set to <span className="info-style modified">wrap</span> or  <span className="info-style modified">wrap-reverse</span> and there's more than one line</span>,
    'wrapFlex' : <span>Wrap flex item onto multiple lines<ol><li><span className="info-attributes">flex-wrap</span> is set to <span className="info-style modified">wrap</span></li></ol></span>,
    '3' : 'Cannot add element',
    'flexContainer' : 'Select/click this container if you want to add other elements inside it \n And click to any empty canvas area to unselect it',
}

export default Canvas
